import React from 'react'
import './app.scss';
import App1 from './App1';
import App2 from './App2';
import {Switch , Route} from 'react-router-dom';

function App() {
    return (
        <div className="app">

        <Switch>
            <Route exact path="/" component={App1}/>
            <Route path="/app2" component={App2}/>
        </Switch>
        </div>
    )
}

export default App
